@import url(https://fonts.googleapis.com/css2?family=Zen+Maru+Gothic:wght@500&display=swap);
header {
    font-family: 'Zen Maru Gothic', Microsoft JhengHei, sans-serif;
    background: #fff;
    display: flex;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
    margin: 0 10px;
}
header ul {
    margin: 0;
    padding: 0;
    flex: 1 1;
    display: flex;
    justify-content: center;    
    grid-gap: 5vw;    
    gap: 5vw;
}

header h1 a {
    color: #00A7BA;
    font-weight: 400;
    margin-left: 50px;
}
header h1 a::before {
    content: "";
    background: url(/twtravel/static/media/whale.b47942e2.png);
    background-size: 100%;
    background-repeat: no-repeat;
    padding: 5px 35px;
    margin-right: 7px;
}

header ul a {
    color: #173565;
}

@media screen and (max-width: 510px) {
    header h1 span {
        display: none;
    }
    header h1 a {
        margin-left: 10px;
    }
    img[width] {
        width: 100%;
    }
}
.banner {
    font-family: 'Zen Maru Gothic', Microsoft JhengHei, sans-serif;
    background-size: cover;
    background-position: center 13%;
    overflow: hidden;
}
.banner .green {
    width: 100%;
    height: 50vh;
    -webkit-backdrop-filter: blur(9px);
            backdrop-filter: blur(9px);
    background: #8bb2;

    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.banner h2 {
    color: #fff;
    font-weight: 400;
    margin: 0 0 10px 0;
    text-shadow: 2px 2px 0 rgba(0,0,0,.13);
    font-size: 4.7em;
}
.banner h3 {
    color: #fff;
    font-weight: 400;
    margin: 0;
    text-shadow: 2px 2px 0 rgba(0,0,0,.13);
    font-size: 1.7em;
    text-transform: uppercase;
}

@media screen and (max-width: 510px) , screen and (max-height: 330px) {
    .banner h2 {
        font-size: 2.7em;
    }
    .banner h3 {
        font-size: .7em;
    }
}
.totop {
    position: fixed;
    bottom: 10px;
    right: 10px;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    align-items: center;
}
.totop .arrow {
    width: 10px;
    height: 10px;
    border-top: 3px solid #fff;
    border-left: 3px solid #fff;
    transform: rotate(45deg);
}
.totop span {
    position: relative;
    font-size: 1.3em;
    color: #fff;
    text-shadow: 1px 1px 0 rgba(0,0,0,.1);
}
footer {
    background: #00A7BA;
    color: #fff;
    text-align: center;
    height: 7vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

footer ul {
    padding: 0;
    margin: 0;
    display: flex;
    grid-gap: 10px;
    gap: 10px;
}

footer li + li::before {
    content: "|";
    margin-right: 10px;
}

footer a {
    color: #fff;
}

[accessKey="Z"]:focus {
    outline-color: #fff;
}

.row {
    box-sizing: border-box;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, minmax(200px, 1fr));
    grid-gap: 18px;
    gap: 18px;
}

@media screen and (max-width: 950px) {
    .row {
        grid-template-columns: repeat(2, minmax(200px, 1fr));
    }
}
@media screen and (max-width: 600px) {
    .row {
        grid-template-columns: repeat(1, minmax(200px, 1fr));
    }
}

.col {
    width: 100%;
    background-color: #fff;
    border-radius: 4px;
    box-shadow: 1px 1px 0 rgba(0,0,0,.018);
    transition: .7s;
}
.col:hover {
    box-shadow: 0 0 11px rgba(0,0,0,.17);
}

article img {
    width: 100%;
    height: 300px;
    object-fit: cover;
    border-radius: 4px 4px 0 0;
    transition: .4s all;
}

.cardbody-image img:hover {
    transform: scale(1.4);
    /* filter: blur(1px); */
}

.cardbody-image {
    overflow: hidden;
    border-radius: 4px 4px 0 0;
}
.cardbody {
    box-sizing: border-box;
    padding: 20px;
}
.cardbody ul {
    padding: 0;
    margin: 0;
}
.cardbody h3 {
    margin: 0 0 10px 0;
}
.cardbody li {
    margin-bottom: 5px;
}

.cardbody-details {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}
.cardbody-photo {
    padding: 10px;
    overflow: hidden;
}
.cardbody-description {
    padding: 10px;
}

.cardbody-details img {
    height: 450px;
    border-radius: 4px;
}

.cardbody-list li {
    margin-bottom: 7px;
}

.info {
    display: flex;
}
.form-searchbar {
    box-sizing: border-box;
    padding: 25px;
    background: #fff;
    margin-bottom: 25px;
    border-radius: 4px;
}
.form-searchbar form {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 10px;
    gap: 10px;
}
.form-searchbar form div:first-child {
    flex: 1 1 200px;
}

.form-searchbar input[type="search"] {
    -webkit-appearance: none;
            appearance: none;
    border: 1px solid #0002;
    padding: 7px;
    border-radius: 4px;
    font-size: 1.3em;
    width: 100%;
}
.form-searchbar select {
    -webkit-appearance: none;
            appearance: none;
    width: 130px;
    padding: 12px;
    text-align: center;
    border-radius: 4px;
    border: 1px solid #0002;
}
input:focus, select:focus, button:focus, a:focus {
    outline: 2px dashed #00A7BA;
}

.search-container {
    box-sizing: border-box;
    border-radius: 4px;
    background-color: #fff;
    padding: 20px;
}

.search-result {
    padding: 0;
}

.search-result > li {
    box-sizing: border-box;
    border-bottom: 1px dashed #00A7BA;
}

.noresult {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    margin: 50px 0 25px;
    color: #00A7BA;
}

.form-searchbar button, .btn-more {
    -webkit-appearance: none;
            appearance: none;
    border-radius: 4px;
    border: none;
    padding: 7px 17px;
    background: #00A7BA;
    color: #fff;
    cursor: pointer;
}

.search-more {
    text-align: center;
}
.breadcrumbs {
    margin: 0;
    background-color: #f1f1f1;
    padding: 17px 2vw;
    display: flex;
    grid-gap: 6px;
    gap: 6px;
}

.breadcrumbs a {
    font-weight: bold;
    color: #00A7BA;
}

.breadcrumbs li+li::before {
    content: ">";
    margin-right: 6px; 
}
body, html {
    margin: 0;
    padding: 0;
    font-family: Microsoft JhengHei, sans-serif;
    color: #173565;
    letter-spacing: .1em;
    scroll-behavior: smooth;
}

a {
    text-decoration: none; 
    color: #173565;
}

main {
    box-sizing: border-box;
    width: 100vw;
    min-height: calc(100vh - 90px);
    padding: 40px 6vw 40px;
    background-color: #f1f1f1;
}

li {
    list-style-type: none;
}

main h2 {
    font-family: 'Zen Maru Gothic', Microsoft JhengHei, sans-serif;
    color: #00A7BA;
    font-weight: 400;
    margin: 25px 0;
}
main h2::before {
    content: "";
    background: url(/twtravel/static/media/Lantern.55f9b7c6.svg);
    padding: 0 18px;
    margin-right: 7px;
}

* {
    animation: fadeIn .5s ease-out;
}
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(15px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    clip: auto
}

i.fa {
    margin-right: 14px;
    width: 20px;
}

.err404 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: calc(100vh - 143px);
}
.err404 div {
    margin-top: 30px;
    font-size: 3em;
    color: #00A7BA;
}

.sitemap li {
    list-style-type: decimal;
    margin-bottom: 10px;
}
